 .canvas {
  position: absolute;
  top: 0;
  pointer-events: none;
}

.scroll-container {
  position: absolute;
  overflow: hidden;
  top: 0px;
  width: 100%;
  height: 100vh;
  font-size: 20em;
  font-weight: 800;
  line-height: 0.9em;
}

#welcome  > svg {
  width: 200px;
